<template>
  <div class="dont">
    <!-- <p class="id">{{ list_data.id }}</p> -->
    <div class="title">
      <h2
        v-bind:class="{ hidden: listTitle }"
        v-on:click="
          (listCrest = true),
            (listTitle = true),
            (listInput = false),
            showTitle()
        "
      >
        {{ list_data.title }}
      </h2>
      <span v-on:click="removeList()" v-bind:class="{ hidden: listCrest }"
        >&#10006;</span
      >
      <input
        id="change"
        type="hidden"
        v-model="title"
        v-on:blur="
          changeListTitle(),
            (listInput = true),
            (listCrest = false),
            (listTitle = false)
        "
        v-on:keypress.enter="
          (changeListTitle(), (listInput = true)),
            (listCrest = false),
            (listTitle = false)
        "
      />
    </div>
    <Card
      v-for="card in cards"
      v-bind:key="card.id"
      v-bind:card_data="card"
      v-bind:list="list_data"
    />
    <!-- <a href="#"
      ><div class="addNew"><p>+ Добавить карточку</p></div></a
    > -->
    <AddCard v-bind:list="list_data" />
  </div>
</template>
<script>
import Card from '@/components/Card.vue';
import AddCard from '@/components/AddCard.vue';
import { mapActions } from 'vuex';

export default {
  name: 'List',
  props: {
    list_data: {
      type: Object,
      default() {
        return {};
      },
    },
    cards: Object,
  },
  components: {
    Card,
    AddCard,
  },
  data() {
    return {
      listInput: true,
      listTitle: false,
      listCrest: false,
      title: this.list_data.title,
    };
  },
  methods: {
    ...mapActions(['DELETE_LIST', 'GET_BOARD', 'RENAME_LIST']),
    // createCard(card) {
    //   this.cards.push(card);
    //   console.log(this.cards);
    // },
    // removeCard() {
    //   this.cards = this.cards.filter((t) => t.id !== id);
    // },
    showTitle() {
      const input = document.querySelector('#change');
      input.type = 'text';
      input.select();
    },
    changeListTitle() {
      const input = document.querySelector('#change');
      this.RENAME_LIST({
        boardId: this.$route.params.board_id,
        listId: this.list_data.id,
        data: { title: input.value, position: 1 },
      });
      input.type = 'hidden';
      this.GET_BOARD(this.$route.params.board_id);
    },
    removeList() {
      this.DELETE_LIST({
        boardId: this.$route.params.board_id,
        listId: this.list_data.id,
      });
      this.GET_BOARD(this.$route.params.board_id);
    },
  },
};
</script>
