<template>
  <div class="main">
    <div class="second" v-bind:class="{ hidden: addList }">
      <a
        href="#"
        v-on:click="(addList = true), (isHidden = false), showInput()"
      >
        <p>+ Добавить список</p>
      </a>
    </div>
    <div class="area" v-bind:class="{ hidden: isHidden }">
      <div class="textarea">
        <form v-on:submit.prevent="addNewList()">
          <input
            type="text"
            id="listName"
            placeholder="Введите название списка"
          />
          <div class="controls">
            <input type="submit" value="Добавить список" />
            <a href="#" v-on:click="(isHidden = true), (addList = false)"
              >&times;</a
            >
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions } from 'vuex';

export default {
  name: 'AddList',
  data() {
    return {
      addList: false,
      isHidden: true,
    };
  },
  methods: {
    showInput() {
      const name = document.getElementById('listName');
      name.focus();
    },
    ...mapActions(['CREATE_LIST', 'GET_BOARD']),
    addNewList() {
      const name = document.getElementById('listName');
      this.CREATE_LIST({
        id: this.$route.params.board_id,
        data: { title: name.value, position: 1 },
      });
      name.value = '';
      // this.addList = false;
      // this.isHidden = true;
      this.GET_BOARD(this.$route.params.board_id);
      console.log('u');
    },
  },
};
</script>
