<template>
  <div class="main">
    <div class="addNew" v-bind:class="{ hidden: addCard }">
      <a href="#" v-on:click="(isHidden = false), (addCard = true), showInput()"
        ><p>+ Добавить карточку</p>
      </a>
    </div>

    <div class="area" v-bind:class="{ hidden: isHidden }">
      <div class="textarea">
        <textarea
          name=""
          id="cardName"
          placeholder="Введите название карточки"
          v-model="title"
        ></textarea>
      </div>
      <div class="controls">
        <input
          type="submit"
          value="Добавить карточку"
          v-on:click="createCard(), (isHidden = true), (addCard = false)"
        />
        <a href="#" v-on:click="(isHidden = true), (addCard = false)"
          >&times;</a
        >
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: 'AddCard',
  props: ['list'],
  data() {
    return {
      title: '',
      isHidden: true,
      addCard: false,
    };
  },
  methods: {
    ...mapActions(['CREATE_CARD', 'GET_BOARD']),
    showInput() {
      document.querySelector('textarea').focus();
    },
    createCard() {
      this.CREATE_CARD({
        boardId: this.$route.params.board_id,
        data: {
          title: this.title,
          list_id: this.list.id,
          position: 1,
        },
      });
      this.GET_BOARD(this.$route.params.board_id);
      this.title = '';
    },
  },
  mounted() {
    document.querySelector('textarea').focus();
  },
};
</script>
