<template>
  <div
    v-on:mouseover="hoverCard = false"
    v-on:mouseout="hoverCard = true"
    class="main"
  >
    <div
      class="second"
      v-bind:class="{ hidden: second }"
      v-on:click="(second = true), showInput(), (span = true)"
    >
      <p>
        {{ card_data.title }}
      </p>
      <!-- <span v-bind:class="{ hidden: hoverCard }" ref="close">&#10006;</span> -->
    </div>
    <div v-bind:class="{ hidden: span }" class="crest">
      <span v-bind:class="{ hidden: hoverCard }" ref="close">&#10006;</span>
    </div>
    <!-- <div class="third" v-bind:class="{ hidden: input }"> -->
    <input
      id="card"
      type="hidden"
      value="card_data.title"
      v-model="title"
      v-on:blur="Change(), (second = false), hideInput(), (span = false)"
      v-on:keypress.enter="
        Change(), (second = false), hideInput(), (span = false)
      "
      ref="myInput"
    />
    <!-- </div> -->
  </div>
</template>
<script>
import { mapActions } from 'vuex';

export default {
  mounted() {
    const vm = this;
    document.addEventListener('click', (item) => {
      if (item.target === vm.$refs.close) {
        vm.deleteCard();
      }
    });
  },
  name: 'Card',
  data() {
    return {
      hoverCard: true,
      second: false,
      span: false,
      title: this.card_data.title,
    };
  },
  props: {
    card_data: {
      type: Object,
      default() {
        return {};
      },
    },
    list: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  methods: {
    ...mapActions(['DELETE_CARD', 'GET_BOARD', 'RENAME_CARD']),
    Change() {
      if (this.title !== '') {
        this.RENAME_CARD({
          boardId: this.$route.params.board_id,
          cardId: this.card_data.id,
          data: {
            title: this.title,
            list_id: this.list.id,
          },
        });
        this.GET_BOARD(this.$route.params.board_id);
        const input = document.querySelector('#card');
        input.type = 'hidden';
      }
    },
    deleteCard() {
      this.DELETE_CARD({
        boardId: this.$route.params.board_id,
        cardId: this.card_data.id,
      });
      this.GET_BOARD(this.$route.params.board_id);
    },
    showInput() {
      const input = document.querySelector('#card');
      input.type = 'text';
      input.select();
    },
    hideInput() {
      // const input = document.querySelector('#card');
      // input.type = 'hidden';
    },
  },
};
</script>
